.mailerglue-admin {

	.mailerglue-primary-bar {
		min-height: 60px;
		background: #45587a;
	}

	.mailerglue-text-regular {
		font-weight: 400 !important;
	}

	.components-panel__row {
		padding: 10px 0;
	}

	.mailerglue-panelbody-form {
		border: 0;
		padding: 0;
		.components-flex {
			width: 100%;
		}
		.components-panel__row {
			margin: 0 !important;
		}
		.components-button {
		    width: 100%;
			text-align: center;
			align-items: center;
			justify-content: center;
			min-height: 44px;
			font-size: 16px;
			line-height: 1.3;
			font-weight: 500;
		}
	}

	.components-input-control__container {
		div.components-input-control__backdrop {
			border-color: rgb(223 223 223);
		}
		input.components-input-control__input {
			height: auto;
			padding: 12px 14px;
			font-size: 14px;
		}
	}

	h4, h2 {
		margin: 0;
		padding: 4px 0;
	}

	p {
		margin: 0;
		padding: 15px 0;
		font-size: 14px;
	}

	p.mailerglue-text-bigger {
		font-size: 15px;
	}

	.mailerglue-setup {
		margin: 0 auto;
		max-width: 500px;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 60px;
	}

	.mailerglue-bottom {
		margin: 0 auto;
		max-width: 500px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 60px;
	}

	.components-notice {
		margin: 0 0 20px;
		padding: 10px 14px;
		border-radius: 3px;
		border: none;
		&.is-error {
			background: #fef08a;
			color: #a16207;
		}
		.components-notice__content {
			margin: 0;
		}
		p {
			padding: 0;
		}
	}

}